.modal {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadein 0.1s linear;
  color: #fff;
}

.modal-container {
  width: 80vmin;
  height: 45vmin;
  background-color: rgba(0, 0, 0, .85);
  border: solid 0.2px #4a4a4a;
  box-shadow: 0 0 0 1px rgba(32, 34, 37, .6), 0 2px 10px 0 rgba(0, 0, 0, .2);
  border-radius: 2px;
  margin: 0 auto;
  pointer-events: all;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  /* transition: all 1s linear; */
  animation: fadein 0.2s linear;
}

.message {
  display: flex;
  flex-grow: 1;
  align-items: center;
  text-align: start;
  width: 72vmin;
  font-size: 3.5vmin;
}

.options {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  border-radius: 0 0 5px 5px;
  border-top: 2px solid #4a4a4a;
  width: 72vmin;
  height: 12vmin;
  font-size: 3.5vmin;
  margin-bottom: 3.5vmin;
  padding-top: 3vmin;
}

.options button {
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  font-size: 3.5vmin;
  min-width: 22vmin;
  padding: 0 3vmin;
}

.options .cancel {
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 9/16) {
  .modal-container {
    width: 44.66vmax;
    height: 25.09vmax;
  }
  .message {
    width: 40.14vmax;
    font-size: 1.95vmax;
  }
  .options {
    width: 40.14vmax;
    height: 6.69vmax;
    font-size: 1.95vmax;
    margin-bottom: 1.95vmax;
    padding-top: 1.67vmax;
  }
  .options button {
    font-size: 1.95vmax;
    min-width: 12.27vmax;
    padding: 0 1.67vmax;
  }
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 1/1) {
  .modal-container {
    width: 44.66vmin;
    height: 25.09vmin;
  }
  .message {
    width: 40.14vmin;
    font-size: 1.95vmin;
  }
  .options {
    width: 40.14vmin;
    height: 6.69vmin;
    font-size: 1.95vmin;
    margin-bottom: 1.95vmin;
    padding-top: 1.67vmin;
  }
  .options button {
    font-size: 1.95vmin;
    min-width: 12.27vmin;
    padding: 0 1.67vmin;
  }
}

@media screen and (min-width: 500px) And (min-height: 891px) {
  .modal-container {
    width: 400px;
    height: 225px;
  }
  .message {
    width: 360px;
    font-size: 17.5px;
  }
  .options {
    width: 360px;
    height: 60px;
    font-size: 17.5px;
    margin-bottom: 17.5px;
    padding-top: 15px;
  }
  .options button {
    font-size: 17.5px;
    min-width: 110px;
    padding: 0 15px;
  }
}




.modal-fullScreen {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadein 0.1s linear;
  color: #fff;
  user-select: none;
}

.modal-fullScreen p {
  font-family: 'Open Sans', sans-serif;
  line-height: normal;
  padding-top: 1.6vmin;
}

.modal-fullScreen-container {
  pointer-events: all;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  animation: fadein 0.4s linear;
  width: -webkit-fill-available;
}

.modal-fullScreen-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3vmin;
}

.modal-fullScreen-body img {
  width: 33vmin;
}

.modal-fullScreen-title {
  font-weight: bold;
  width: 100%;
  font-size: 4.5vmin;
  margin: 4vmin 0 3.5vmin 0;
  text-align: center;
}

.modal-fullScreen-message {
  font-size: 3vmin;
}

.modal-fullScreen-footer {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.modal-fullScreen-footer-message {
  padding-left: 2vmin;
  font-size: 2vmin;
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 9/16) {
  /* VMAX */
  .modal-fullScreen p {
    padding-top: 0.89vmax;
  }
  .modal-fullScreen-container {
    width: 44.6vmax;
  }
  .modal-fullScreen-body {
    padding-top: 1.67vmax;
  }
  .modal-fullScreen-body img {
    width: 18.4vmax;
  }
  .modal-fullScreen-title {
    font-size: 2.51vmax;
    margin: 2.51vmax 0 1.95vmax 0;
  }
  .modal-fullScreen-message {
    font-size: 1.67vmax;
  }
  .modal-fullScreen-footer-message {
    padding-left: 1.12vmax;
    font-size: 1.12vmax;
  }
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 1/1) {
  /* VMIN */
  .modal-fullScreen p {
    padding-top: 0.89vmin;
  }
  .modal-fullScreen-container {
    width: 44.6vmin;
  }
  .modal-fullScreen-body {
    padding-top: 1.67vmin;
  }
  .modal-fullScreen-body img {
    width: 18.4vmin;
  }
  .modal-fullScreen-title {
    font-size: 2.51vmin;
    margin: 2.51vmin 0 1.95vmin 0;
  }
  .modal-fullScreen-message {
    font-size: 1.67vmin;
  }
  .modal-fullScreen-footer-message {
    padding-left: 1.12vmin;
    font-size: 1.12vmin;
  }
}

@media screen and (min-width: 500px) And (min-height: 891px) {
  .modal-fullScreen p {
    padding-top: 8px;
  }
  .modal-fullScreen-container {
    width: 400px;
  }
  .modal-fullScreen-body {
    padding-top: 15px;
  }
  .modal-fullScreen-body img {
    width: 165px;
  }
  .modal-fullScreen-title {
    font-size: 22.5px;
    margin: 20px 0 17.5px 0;
  }
  .modal-fullScreen-message {
    font-size: 15px;
  }
  .modal-fullScreen-footer-message {
    padding-left: 10px;
    font-size: 10px;
  }
}